  color: rgb(130 130 130 / var(--tw-text-opacity));
}

input[type="text"],input[type="password"] {
  border-radius: 8px;
  border-width: 1px;
  --tw-border-opacity: 1;
  border-color: rgb(224 224 224 / var(--tw-border-opacity));
  padding-left: 16px;
  padding-right: 16px;
  padding-top: 12px;
  padding-bottom: 12px;
  font-size: 1.6rem;
}


.input-header {
  margin-left: 8px;
}

button {
  border-radius: 8px;
  border-width: 2px;
  --tw-border-opacity: 1;
  border-color: rgb(210 31 60 / var(--tw-border-opacity));
  --tw-bg-opacity: 1;
  background-color: rgb(210 31 60 / var(--tw-bg-opacity));
  padding-top: 12px;
  padding-bottom: 12px;
  font-size: 1.8rem;
  font-weight: 500;
  --tw-text-opacity: 1;
  color: rgb(255 255 255 / var(--tw-text-opacity));
  transition-property: all;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 300ms;
}

button:hover {
  --tw-bg-opacity: 1;
  background-color: rgb(255 255 255 / var(--tw-bg-opacity));
  --tw-text-opacity: 1;
  color: rgb(210 31 60 / var(--tw-text-opacity));
}

